class Star {
    constructor(options) {
        this.name = options[0].name,
        this.apparentMagnitude = options[0].apparent_magnitude;
        this.absoluteMagnitude = options[0].absolute_magnitude;
        this.distance = options[0].distance_light_year;
        this.declination = options[0].declination;
        this.rightAscension = options[0].right_ascension;
        this.constellation = options[0].constellation;
        this.spectralClass = options[0].spectral_class;
    }
}

export default Star;